import React, { useState, useEffect } from "react"
import back from "../../static/images/back.svg";
import more from "../../static/images/more.svg";

const menu = [
    "Start",
    "Numer telefonu komórkowego",
    "Kod weryfikacyjny",
    "Twoje dane",
    "Twoje dane adresowe",
    "Nazwisko panieńskie matki",
    "Twój adres e-mail",
    "Twoje dane majątkowe",
    "Oświadczenia",
    "Gratulacje!",
]

const Header = ({ handlePreviousStep, percent, state, setState }) => {
    const [calculatedPercent, setCalculatedPercent] = useState(0);


    useEffect(() => { // calculate percents for desktop
        if (window.getComputedStyle(document.querySelector('.header-left')).getPropertyValue('display') !== 'none') {
            const leftHeader = document.querySelector('.header-menu_container').offsetHeight;
            const items = document.querySelector('.header-menu_container').querySelectorAll('li');
            if (state === 9) {
                setCalculatedPercent(100);
                return;
            }
            if (state == 0) {
                setCalculatedPercent(0);
                return;
            }

            setCalculatedPercent(items[--state].offsetTop * 100 / leftHeader);
        }





    }, [state])

    return (
        <>
            <header className="header-left">

                <div className="header-menu_container">
                    <nav>
                        <ul>
                            {menu.map((element, index) => {
                                if (index === 0) return null;
                                if (index < state)
                                    return (
                                        <li key={index} onClick={() => setState(index)} className="menu_item-completed">  {element} </li>
                                    )
                                if (index === state)
                                    return (
                                        <li key={index} className="menu_item-active"> {element} </li>
                                    )
                                if (index > state)
                                    return (
                                        <li key={index} className="menu_item-disabled"> {element} </li>
                                    )
                            })}
                        </ul>
                    </nav>
                </div>
                <div className="header-progress_container">
                    <progress
                        className="progress is-primary"
                        value={calculatedPercent}
                        max={100}
                    >
                        {calculatedPercent + "%"}
                    </progress>
                </div>
            </header>
            <header className="header-top">
                <div className="header-buttons_container">
                    <button name="button_back"
                        onClick={() => handlePreviousStep()}
                    >
                        <img
                            src={back}
                        />
                        <p> Back </p>
                    </button>
                    <h2 className="header_title"> Wniosek  <input id="hack" inputMode="tel" /> </h2>

                    <button
                        onClick={() => window.location.reload()}
                    >
                        <img className="header_hamburger" src={more} />
                    </button>
                </div>


                <div className="header-progress_container">
                    <progress
                        className="progress is-primary"
                        value={percent || 0}
                        max={100}
                    >
                        {percent + "%"}
                    </progress>
                </div>
            </header>
        </>
    )
}

export default Header
