import React, { useState, useEffect, useRef } from 'react';
import { Field } from 'react-final-form';
import { OnChange, OnBlur } from 'react-final-form-listeners'
import { isApple } from '../lib/utils';

const Opts = ({ name, children, className, setIsLabel, handleOnChange, handleValue }) => {
    const ref = useRef();

    useEffect(() => {
        console.log(ref.current)
        if (ref.current.value !== "default") {
            setIsLabel(true);
        } else {
            setIsLabel(false);
        }

    }, [])

    const onChange = (e) => {
        if (e.target.value !== "default") {
            setIsLabel(true);
        } else {
            setIsLabel(false);
        }
    }

    return (
        <select name={name} className={className} value={handleValue} ref={ref} onChange={(e) => { handleOnChange(e); onChange(e); }}>
            {children}
        </select>
    )
};


const Select = ({ name, children, onChangeHandler, defaultLabel }) => {

    const [isLabel, setIsLabel] = useState(false);

    return (
        <fieldset>
            <Field defaultValue="default" name={name} component="select" className="select select--filed" >
                {({ input, meta, className }) => (
                    <>

                        {/* <OnChange name={name}>
                            {(value, previous) => {
                                onChangeHandler && onChangeHandler(value)

                                if (value !== "default") {
                                    setIsLabel(true);
                                } else {
                                    setIsLabel(false);
                                }
                            }}
                        </OnChange> */}
                        <Opts
                            name={input.name}
                            className={className}
                            setIsLabel={(value) => setIsLabel(value)}
                            handleOnChange={(e) => { input.onChange(e) }}
                            handleValue={input.value}

                        >

                            {isApple() && (<optgroup>
                                {children}
                            </optgroup>)}

                            {!isApple() && children}
                        </Opts>


                    </>
                )}





            </Field>
            {isLabel && <label className="moving_label" htmlFor={name}> {defaultLabel} </label>}
            <p className="virtual_paragraph_to_scroll" id={`scroll-${name}`} aria-hidden="true"> scroll </p>
        </fieldset>
    )
}

export default Select;

