import React, { useEffect, useState } from "react";
import { Link } from "gatsby"
import Input from '../Input';
import info from "../../../static/images/info.svg";
import { formatNumber, formatCode } from '../../lib/formats';
import Select from '../Select';
import { countries } from '../../../data/countries';


const Wealth = () => {

    const [otherWealthSource, setOtherWealthSource] = useState(false);

    const selectWelthSourceHandler = (value, previous) => {
        if (value === "other") {
            setOtherWealthSource(true);
        } else {
            setOtherWealthSource(false);
        }
    }

    return (
        <div className="step-container">

            <h2 className="title"> Twoje dane majątkowe </h2>

            <div className="names-inputs">
                <Select name="wealth_value">
                    <option value="default" defaultValue disabled > Wartość majątku </option>
                    <option value="small"> Poniżej 100000 PLN</option>
                    <option value="medium"> Pomiędzy 100&nbsp;000 -&nbsp;500&nbsp;000 PLN </option>
                    <option value="large"> Powyżej 500 000 PLN </option>
                </Select>





                <Select name="wealth_source" onChangeHandler={(value, previus) => selectWelthSourceHandler(value, previus)}>
                    <option value="default" defaultValue disabled> Źródło majątku </option>
                    <option value="wynagrodzenie"> Wynagrodzenie </option>
                    <option value="spolki"> przychody z udziałów w spółkach </option>
                    <option value="inwestycje"> przychody inwestycyjne </option>
                    <option vlaue="darowizny"> spadek/otrzymane darowizny </option>
                    <option value="hazard"> hazard</option>
                    <option value="cryptowaluty"> cryptowaluty </option>
                    <option value="other"> inne </option>
                </Select>

                {otherWealthSource && (
                    <Input
                        className="input--moving_label "
                        mode=""
                        name="wealth_source--extra"
                        // validate={[value => (value ? undefined : "required"), value => { return value.length >= 3 ? undefined : "bad length" }]}
                        label="Inne źródło dochodu"
                    />
                )}



                <Select name="wealth_country">
                    <option value="default" defaultValue disabled> Pochodzenie majątku </option>
                    {countries.map((country, index) => (
                        <option key={index} value={country.number} > {country.flag} {" "} {country.name}  </option>
                    ))}
                </Select>


            </div>

            <p className="description">
                <img src={info} /> <span> Musimy Cię o to zapytać z powodów formalnych, nie będziemy wykorzystywać tych informacji w innych celach </span>
            </p>



        </div>
    )
}

export default Wealth;