import React from "react"
import { Form } from 'react-final-form'
import { GetGlobalState } from './Context';
import Button from './Button';


const FormComponent = ({ state, nextStep, children }) => {

    const [{ form }, dispatch] = GetGlobalState();

    return (
        <Form
            initialValues={form}
            onSubmit={(data) => {

                dispatch("set", { data: data });
                nextStep();
                console.log(data);
            }}
            render={({ handleSubmit, invalid, pristine, values }) => {
                return (
                    <>
                        <form onSubmit={handleSubmit}
                            className={`form_container-${children[state].props.path}`}
                            autoComplete="false"
                        >

                            {children[state]}

                            <div
                                className="container-button"

                            >
                                <Button classList="button--continue" type="submit" disabled={invalid} > {children[state].props.customButton || "Przejdź Dalej "} </Button>
                            </div>
                            {/* <p className="empty-space"> </p> */}
                            <div className="test"></div>

                        </form>
                    </>
                )
            }

            }
        />
    )
}

export const submitForm = () => {
    document.querySelector('form')
        .dispatchEvent(new Event('submit', { cancelable: true }));
}

export default FormComponent;