import React, { useState, useEffect, useRef } from "react"
import { SwitchTransition, CSSTransition } from "react-transition-group"
import Header from "./Header"
import Button from "./Button"
import { disableBodyScroll, enableBodyScroll } from "body-scroll-lock"
import { isApple, isMobileAndTablet } from "../lib/utils"
import { navigate, Router } from "@reach/router"
import Form, { submitForm } from "./Form";

const Layout = ({ children }) => {
    const [state, setState] = useState(0);
    const layout = useRef();

    const countOfSteps = children.length
    const percent = children[state].props.percent

    const [windowInnerHeight, setWindowInnerHeight] = useState(null)
    const windowInnerHeightRef = useRef(windowInnerHeight)

    const scrollElementIntoView = element => {

        if (true
            // (windowInnerHeightRef.current -
            //   element.getBoundingClientRect().height -
            //   document.querySelector("header").getBoundingClientRect().height) /
            // 3 <
            // element.getBoundingClientRect().top
        ) {
            // element.value = element.getBoundingClientRect().top
            document.getElementById(`scroll-${element.name}`).scrollIntoView()
        }

    }

    const nextStep = () => {
        if (state < countOfSteps - 1) {
            setState(state => ++state);
            document.querySelector(".test").style.display = "none"
        }

        return;
    }


    const previousStep = () => {
        if (state > 0) {
            setState(state => --state);

            document.querySelectorAll('input').forEach((input) => {
                console.log(input)
                if (input.value.length > 0)
                    input.classList.add("input--filed")
                else
                    input.classList.remove("input--filed")
            })
        }

    }



    useEffect(() => {
        const childPath = children[state].props.path


        window.addEventListener('popstate', (event) => {
            const index = children.findIndex((child, index) => "?step=" + child.props.path === event.path[0].location.search)

            if (index !== - 1) setState(index);

            return;
        });

        navigate(`/?step=${childPath}`)



    }, [state])


    useEffect(() => {
        setWindowInnerHeight(window.innerHeight)
        windowInnerHeightRef.current = window.innerHeight

        let lastFocused = {
            object: null,
            count: 0,
        };

        document.addEventListener("focusin", e => {
            if (isApple()) {
                if (e.target.id !== "hack" && e.target !== lastFocused.object && lastFocused.count < 2) {
                    lastFocused.object = e.target;
                    lastFocused.count = lastFocused.count + 1;
                    if (e.target.tagName.toUpperCase() === "INPUT" || e.target.tagName.toUpperCase() === "SELECT") {
                        document.querySelector(".test").style.display = "block";
                        document.getElementById("hack").focus()

                        let timer = setTimeout(() => {
                            scrollElementIntoView(e.target)

                            e.target.focus()
                        }, 50)

                        let timer2 = setTimeout(() => {
                            lastFocused = {
                                object: null,
                                count: 0
                            }
                        }, 70)

                        e.target.addEventListener("focusout", a => {
                            if (document.querySelector(".test") && !a.relatedTarget) {
                                document.querySelector(".test").style.display = "none"
                            }
                        })

                        return () => {
                            clearTimeout(timer);
                            clearTimeout(timer2);
                        }
                    }
                    else {
                        if (document.querySelector(".test")) {
                            document.querySelector(".test").style.display = "none"
                        }
                    }
                }

            }



        })

        document.addEventListener("focusin", e => {
            if (isApple()) {
                if (e.target.tagName.toUpperCase() === "INPUT") {
                    if (document.querySelector(".test")) {
                        document.querySelector(".test").style.display = "block"
                    }
                }
            }
        })


    }, [])


    useEffect(() => {
        function setDocHeight() {
            document.documentElement.style.setProperty(
                "--vh",
                `${window.innerHeight / 100}px`
            )
        }

        document.addEventListener("keydown", e => {
            if (isMobileAndTablet) {
                if (e.keyCode == 13) {
                    e.preventDefault();
                    let flagPreventNext = false;

                    if (isApple()) {
                        let inputs = document.querySelectorAll('.input');
                        if (inputs) {
                            Array.from(inputs).some(input => {
                                if (!input.value) {
                                    scrollElementIntoView(input);

                                    let timer = setTimeout(() => {
                                        input.focus();
                                    }, 50)
                                    flagPreventNext = true;
                                    return true;
                                }
                            })
                        }
                    }


                    if (!flagPreventNext) {
                        submitForm();
                    }



                }
            } else {
                submitForm();
            }

        })

        setDocHeight()
        disableBodyScroll(document.querySelector(".main"));

        return () => {
            enableBodyScroll(document.querySelector(".main"));
        }



    }, [])


    return (
        <>
            <div className="layout" ref={layout}>
                <Header handlePreviousStep={() => previousStep()} setState={(index) => setState(index)} percent={percent} state={state} />
                <main className="main">
                    <SwitchTransition>
                        <CSSTransition
                            key={state}
                            addEndListener={(node, done) =>
                                node.addEventListener("transitionend", done, false)
                            }
                            classNames="fade"
                        >
                            <Form state={state} nextStep={() => nextStep()}>
                                {children}

                            </Form>

                        </CSSTransition>
                    </SwitchTransition>
                </main>
            </div>
        </>
    )
}

export default Layout
